.toggle-button{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 30px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}
.toggle-button_line{
    width: 30px;
    height: 2px;
    background-color: white;
}

@media (min-width: 769px){
    .toggle-button{
        display: none;
    }
}