.side-drawer {
    height: 100%;
    background-color: white;
    box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index:200;
}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: 0.5rem 0;
}

.side-drawer a {
    text-decoration: none;
    color: rgb(194, 26, 54);
    font-size: 1.2rem;
    text-shadow: 1px 1px 2px black; 

}

.side-drawer a:hover, .side-drawer a:active {
    color: rgb(221, 106, 106);
    text-shadow: 1px 1px 1px black; 
}

@media (min-width: 769px){
    .side-drawer{
        display: none;
    }
}